import { HttpClient } from "../../config/http";
import { DashboardModel } from "../../models/Dashboard";
import { ConfigurationModel } from "../../models/Configuration";
import { SongModel } from "../../models/Song";
import { CampaignModel } from "../../models/Campaign";
import { AdModel } from "../../models/Ads";

export default class AdminService extends HttpClient {
  public resource = "admin";

  dashboard = async (): Promise<DashboardModel> => {
    const response = await this.get("dashboard");
    return response.data as DashboardModel;
  };

  configuration = async (): Promise<ConfigurationModel> => {
    const response = await this.get("configuration");
    return response.data as ConfigurationModel;
  };

  updateConfiguration = async (payload: ConfigurationModel): Promise<ConfigurationModel> => {
    const response = await this.patch("configuration", payload);
    return response.data as ConfigurationModel;
  };

  fetchUsers = ({
    page,
    limit,
    q,
    status,
    canWithdraw,
    userType,
    hasRunCampaigns,
    timeframe
  }: {
    page: number;
    limit: number;
    q: string | null;
    status: boolean;
    canWithdraw: boolean;
    userType: string;
    hasRunCampaigns?: boolean;
    timeframe?: string;
  }) => {
    return this.get("users", { page, limit, q, status, canWithdraw, userType, hasRunCampaigns, timeframe });
  };

  fetchCampaigns = ({ page, limit, status }: { page: number; limit: number; status?: string }) => {
    return this.get("campaigns", { page, limit, status });
  };

  fetchCampaign = (id: string) => {
    return this.get(`campaigns/${id}`);
  };

  toggleCampaignInfo = (id: string) => {
    return this.post(`${id}/toggle-show-info`);
  };

  fetchReviews = (songId: string, { page, limit }: { page: number; limit: number }) => {
    return this.get(`reviews/${songId}`, { page, limit });
  };

  fetchSongs = ({ page, limit }: { page: number; limit: number }) => {
    return this.get("songs", { page, limit });
  };

  // createSong = (dto: SongModel) => {
  //   return this.post("songs", dto);
  // };

  createSong = (dto: FormData) => {
    return this.post("songs", dto);
  };

  updateSong = (id: string, dto: FormData) => {
    return this.patch(`songs/${id}`, dto);
  };

  createCampaign = (dto: CampaignModel) => {
    return this.post("campaigns", dto);
  };

  fetchUser = (userId: string) => {
    return this.get(`users/${userId}`);
  };

  // fetchUserInfo = (userId: string, { page, limit, type }: { page: number; limit: number; type: string }) => {
  //   return this.get(`user/${userId}`, {
  //     page,
  //     limit,
  //     type
  //   });
  // };

  fetchUserReferrals = (userId: string, { page, limit }: { page: number; limit: number }) => {
    return this.get(`users/${userId}/referrals`, {
      page,
      limit
    });
  };

  fetchUserTransactions = (userId: string, { page, limit }: { page: number; limit: number }) => {
    return this.get(`users/${userId}/transactions`, {
      page,
      limit
    });
  };

  disableUser = ({ userId, disabledReason }: { userId: string; disabledReason: string }) => {
    return this.post(`users/disable`, { userId, disabledReason });
  };

  enableUser = (userId: string) => {
    return this.post(`users/${userId}/enable`);
  };

  sendEmail = (payload: any) => {
    return this.post(`send-email`, payload);
  };

  createAd = (dto: FormData) => {
    return this.post("ads", dto);
  };

  updateAd = (id: string, dto: FormData) => {
    return this.patch(`ads/${id}`, dto);
  };

  enableAd = (adId: string) => {
    return this.patch(`ads/enable-ad/${adId}`);
  };

  disableAd = (adId: string) => {
    return this.patch(`ads/disable-ad/${adId}`);
  };

  fetchAds = ({ page, limit }: { page: number; limit: number }) => {
    return this.get(`ads`, { page, limit });
  };

  fetchMusicPlatforms = ({ page, limit }: { page: number; limit: number }) => {
    return this.get(`music-platforms/stats`, { page, limit });
  };
}

export const adminService = new AdminService();
