


























import { Component, Vue } from "vue-property-decorator";
import { FormEvents } from "../constants";
import { SongModel } from "../models/Song";
import { adminService } from "../modules/admin/admin.api";
import { GlobalEvent } from "../shared/helpers";

@Component
export default class EmailForm extends Vue {
  payload = {
    userId: null,
    disabledReason: null
  };

  mounted() {
    GlobalEvent.$on(FormEvents.SH_SEND_EMAIL, (data: any) => {
      this.payload = data;
      this.modal("email-form", true);
    });
  }

  async beforeSave() {
    try {
      await this.validate();
      this.$swal({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, send it!"
      }).then(async (result: any) => {
        if (result.value) {
          await this.save(this.payload);
          this.$swal({
            title: "Success!",
            text: "Email has been sent.",
            type: "success"
          });
        }
      });
    } catch (error) {
      this.handleError(error);
    }
  }

  async save(payload: Record<string, any>) {
    try {
      this.isLoading = true;
      await adminService.sendEmail(payload);
      this.isLoading = false;
      this.modal("email-form", false);
    } catch (error) {
      this.handleError(error);
    }
  }
}
