







































import { mixins } from "vue-class-component";
import { Component, Prop, Vue } from "vue-property-decorator";
import { adminService } from "../../modules/admin/admin.api";
import { formatDate } from "../../shared/helpers";
import { DataTableMixin } from "../../shared/mixins/datatable.mixin";
import { TransactionStatus } from "../../models/Transaction";

@Component
export default class TransactionsTable extends mixins(DataTableMixin) {
  @Prop() userId!: string;

  transactions: any[] = [];

  headers = [
    {
      name: "operation",
      prop: "operation"
    },
    {
      name: "amount",
      prop: "amount"
    },
    {
      name: "status",
      prop: "status"
    },
    {
      name: "Date",
      prop: "createdAt",
      formatter: (data: string) => formatDate(data, "DD MMMM YYYY, hh:mm a")
    }
  ];

  created() {
    this.fetchData();
  }

  async fetchData() {
    try {
      this.isLoading = true;
      const response = await adminService.fetchUserTransactions(this.userId, {
        page: this.pagination.page,
        limit: this.pagination.perPage
      });

      this.transactions = response.data;
      this.pagination = { ...this.pagination, ...response.pagination };
      this.isLoading = false;
    } catch (error) {
      this.handleError(error);
    }
  }

  getStatusColor(status: TransactionStatus) {
    let color: string = "";
    switch (status) {
      case TransactionStatus.PENDING:
        color = "alert-warning";
        break;
      case TransactionStatus.SUCCESSFUL:
        color = "alert-success";
        break;
      case TransactionStatus.FAILED:
        color = "alert-danger";
        break;
      case TransactionStatus.CANCELLED:
        color = "alert-info";
        break;

      default:
        break;
    }

    return color;
  }
}
