







































































import { mixins } from "vue-class-component";
import { Component, Vue } from "vue-property-decorator";
import { LoginUser } from "./authentication.api";
import { UserLoginDTO } from "./authentication.model";
import { authenticationStore } from "./authentication.module";

@Component
export default class Login extends Vue {
  payload: UserLoginDTO | {} = {};
  isLoading: boolean = false;

  async created() {
    // await this.loadDependencyJS([
    //   '/assets/plugins/p-scroll/perfect-scrollbar.js',
    //   '/assets/plugins/p-scroll/pscroll.js',
    //   '/assets/plugins/p-scroll/pscroll-1.js',
    // ]);
    this.loadJs();
  }

  async login(payload: UserLoginDTO) {
    try {
      await this.validate();
      this.isLoading = true;
      await authenticationStore.Login(payload);
      this.$router.push("/");
      this.toastSuccess("Login Success");
    } catch (error) {
      this.isLoading = false;
      this.handleError(error);
    }
  }
}
