




































import { mixins } from "vue-class-component";
import { Component, Prop, Vue } from "vue-property-decorator";
import { adminService } from "../../modules/admin/admin.api";
import { formatDate } from "../../shared/helpers";
import { DataTableMixin } from "../../shared/mixins/datatable.mixin";

@Component
export default class ReferralsTable extends mixins(DataTableMixin) {
  @Prop() userId!: string;

  referrals: any[] = [];

  headers = [
    {
      name: "fullName",
      prop: "fullName"
    },
    {
      name: "email",
      prop: "email"
    },
    {
      name: "verified",
      prop: "verified"
    },
    {
      name: "Date",
      prop: "createdAt",
      formatter: (data: string) => formatDate(data, "DD MMMM YYYY, hh:mm a")
    }
  ];

  created() {
    this.fetchData();
  }

  async fetchData() {
    try {
      this.isLoading = true;
      const response = (await adminService.fetchUserReferrals(this.userId, {
        page: this.pagination.page,
        limit: this.pagination.perPage
      })) as any;

      this.referrals = response.data;
      this.$emit("referral", this.referrals);
      this.pagination = { ...this.pagination, ...response.pagination };
      this.isLoading = false;
    } catch (error) {
      this.handleError(error);
    }
  }
}
