



























import { Component, Vue } from "vue-property-decorator";
import { FormEvents } from "../constants";
import { SongModel } from "../models/Song";
import { adminService } from "../modules/admin/admin.api";
import { GlobalEvent } from "../shared/helpers";

@Component
export default class DisableUser extends Vue {
  payload = {
    userId: null,
    disabledReason: null
  };

  mounted() {
    GlobalEvent.$on(FormEvents.SH_DISABLE_USER, (data: any) => {
      this.payload = data;
      this.modal("disable-user-form", true);
    });
  }

  async beforeSave(payload: any) {
    await this.validate();
    this.confirm(() => this.save(payload));
  }

  async save(payload: Record<string, any>) {
    try {
      this.isLoading = true;
      const response = await adminService.disableUser({
        userId: payload.userId,
        disabledReason: payload.disabledReason
      });
      this.isLoading = false;
      this.toastSuccess(response.message);
      this.modal("disable-user-form", false);
    } catch (error) {
      this.handleError(error);
    }
  }
}
