import { Component, Vue } from "vue-property-decorator";
import { FormEvents } from "../constants";
import { UserModel } from "../models/User";
import { adminService } from "../modules/admin/admin.api";
import { formatDate, GlobalEvent } from "../shared/helpers";
import { DataTableMixin } from "../shared/mixins/datatable.mixin";
import ReferralsTable from "./components/ReferralsTable.vue";
import TransactionsTable from "./components/TransactionsTable.vue";

@Component({
  components: {
    ReferralsTable,
    TransactionsTable
  }
})
export default class SingleUser extends mixins(DataTableMixin) {
  pagination = {
    page: 1,
    pageCount: 0,
    perPage: 10,
    total: 0,
    skipped: 0,
    type: "referrals"
  };

  user: Partial<UserModel> = {};

  userId: string | null = null;

  referral: any | string = null;

  referrals: any[] = [];

  mounted() {
    const userId = this.$route.params.userId;
    this.userId = userId;
    this.fetchUser();
  }

  async fetchUser() {
    try {
      this.showLoading();
      const response = await adminService.fetchUser(this.userId as string);
      this.user = response.data;
      // await this.fetchUserInfo();
      this.hideLoading();
    } catch (error) {
      this.handleError(error);
    }
  }

  showDisableUserForm(userId: string) {
    GlobalEvent.$emit(FormEvents.SH_DISABLE_USER, {
      userId
    });
  }

  async enableUser(userId: string) {
    await this.confirm(() => adminService.enableUser(userId), "User is enabled");
    this.fetchUser();
  }

