



























































































































































































import { Component, Vue } from "vue-property-decorator";
import { CampaignModel, CampaignStatus } from "../models/Campaign";
import { adminService } from "../modules/admin/admin.api";
import { formatDate, GlobalEvent, moneyFormat } from "../shared/helpers";
import CampaignForm from "../modals/CampaignForm.vue";
import SingleCampaign from "../modals/SingleCampaign.vue";
import { FormEvents } from "../constants";
import { apiConfigService } from "../config/api.config";
import { mixins } from "vue-class-component";
import { DataTableMixin } from "../shared/mixins/datatable.mixin";

@Component({
  components: {
    CampaignForm,
    SingleCampaign
  }
})
export default class Campaigns extends mixins(DataTableMixin) {
  campaigns: CampaignModel[] = [];

  CampaignStatus = CampaignStatus;

  pagination = {
    page: 1,
    pageCount: 0,
    perPage: 10,
    total: 0,
    skipped: 0,
    status: undefined
  };

  headers = [
    {
      name: "Artist",
      prop: "artisteName"
    },
    {
      name: "Song",
      prop: "song"
    },
    {
      name: "Budget",
      prop: "budget"
    },
    {
      name: "Commission",
      prop: "amountPerListen"
    },
    {
      name: "Available Listens",
      prop: "allocatedListens",
      formatter: (item: number) => moneyFormat(item)
    },
    // {
    //   name: "Total Listens",
    //   prop: "listensCount",
    //   formatter: (item: number) => moneyFormat(item)
    // },
    {
      name: "Mode",
      prop: "mode"
      // formatter: (item: number) => moneyFormat(item)
    },

    {
      name: "Start Date",
      prop: "createdAt",
      formatter: (data: string) => formatDate(data, "DD MMMM YYYY, hh:mm a")
    },
    {
      name: "status",
      prop: "status"
      // formatter: (item: number) => moneyFormat(item)
    },
    {
      name: "Show metrics",
      prop: "showCampaignInfo"
      // formatter: (item: number) => moneyFormat(item)
    }
  ];

  fetchData() {
    this.handleFetchCampaigns();
  }

  mounted() {
    this.handleFetchCampaigns();
  }

  async handleFetchCampaigns() {
    try {
      this.showLoading();
      const response = await adminService.fetchCampaigns({
        page: this.pagination.page,
        limit: this.pagination.perPage,
        status: this.pagination.status
      });

      this.campaigns = response.data;

      this.pagination = { ...this.pagination, ...response.pagination };
      this.hideLoading();
    } catch (error) {
      this.handleError(error);
    }
  }

  // async myCallback(page: number) {
  //   this.pagination.page = page;
  //   this.handleFetchCampaigns();
  // }

  // async handleLimitChange(limit: number) {
  //   this.pagination.perPage = limit;
  //   this.handleFetchCampaigns();
  // }

  getStatusColor(status: CampaignStatus) {
    let color: string = "";
    switch (status) {
      case CampaignStatus.PENDING:
        color = "alert-warning";
        break;
      case CampaignStatus.ACTIVE:
        color = "alert-success";
        break;
      case CampaignStatus.CANCELLED:
        color = "alert-danger";
        break;
      case CampaignStatus.COMPLETED:
        color = "alert-info";
        break;

      case CampaignStatus.AWAITING_PAYMENT:
        color = "alert-secondary";
        break;
      default:
        break;
    }

    return color;
  }

  showModal(data: CampaignModel) {
    GlobalEvent.$emit(FormEvents.SH_CAMPAIGN_FORM, data);
  }

  showSingleModal(id: string) {
    console.log({ id });
    GlobalEvent.$emit(FormEvents.SH_CAMPAIGN, id);
  }

  async notifyUsers() {
    try {
      this.showLoading();
      await apiConfigService.request().post("admin/campaign-notify");
      this.hideLoading();
      this.toastSuccess("Notification sent");
    } catch (error) {
      this.handleError(error);
    }
  }

  async toggleCampaignInfo(id: string) {
    try {
      this.showLoading();
      await adminService.toggleCampaignInfo(id);
      this.hideLoading();
      this.toastSuccess("Success");
    } catch (error) {
      this.handleError(error);
    }
  }

  copyLink(event: any) {
    var Url = this.$refs.metrics_link as any;

    const clipboardData = event.clipboardData || event.originalEvent?.clipboardData || navigator.clipboard;
    clipboardData.writeText(Url.href);
    this.toastSuccess("Link copied");
  }

  async publish(campaignId: string) {
    this.confirm(async () => {
      try {
        this.showLoading();
        await apiConfigService.request().post(`admin/campaigns/${campaignId}/publish`);
        this.handleFetchCampaigns();
      } catch (error) {
        this.handleError(error);
      } finally {
        this.hideLoading();
      }
    });
  }

  clearFilters() {
    this.pagination.status = undefined;
    this.handleFetchCampaigns();
  }
}
