import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import ProtectedPages from "@/containers/index.vue";
import LoginScreen from "@/modules/authentication/login.vue";
import DashboardScreen from "@/views/Dashboard.vue";
import Songs from "@/views/Songs.vue";
import Users from "@/views/Users.vue";
import SingleUser from "@/views/User.vue";
import Campaigns from "@/views/Campaigns.vue";
import MusicPlatforms from "@/views/MusicPlatforms.vue";
import SingleCampaign from "@/views/SingleCampaign.vue";
import Ads from "@/views/Ads.vue";
import { AppGuard } from "./router.guard";
Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  { path: "/login", name: "LoginScreen", component: LoginScreen },
  { path: "/campaigns/:campaignId", name: "SingleCampaign", component: SingleCampaign },
  {
    path: "/",
    component: ProtectedPages,
    beforeEnter: AppGuard,
    children: [
      { path: "/", name: "DashboardScreen", component: DashboardScreen },
      { path: "/songs", name: "Songs", component: Songs },
      { path: "/users", name: "Users", component: Users },
      { path: "/users/:userId", name: "SingleUser", component: SingleUser },
      { path: "/campaigns", name: "Campaigns", component: Campaigns },
      { path: "/music-platforms", name: "MusicPlatforms", component: MusicPlatforms },
      { path: "/ads", name: "Ads", component: Ads }
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
