import { SongModel } from "./Song";

export interface Metadata {}

export interface CampaignModel {
  listensCount: number;
  id: string;
  createdAt: Date;
  updatedAt: Date;
  deletedAt?: any;
  songId: string;
  createdById: string;
  budget: number;
  amountPerListen: number | null;
  allocatedListens: number;
  status: CampaignStatus;
  metadata: Metadata;
  song: SongModel;
  listenBudgetPercentage: number | null;
  fansCount: number | null;
  musicPlatformId: string;
  mode: string;
  loadDate: Date;
  fanConversionRate: number;
}

export enum CampaignStatus {
  PENDING = "Pending",
  ACTIVE = "Active",
  CANCELLED = "Cancelled",
  COMPLETED = "Completed",
  AWAITING_PAYMENT = "Awaiting Payment"
}
