









































































































































































import { Component, Vue } from "vue-property-decorator";
import { ConfigurationModel } from "../models/Configuration";
import { GlobalEvent } from "../shared/helpers";
import { FormEvents } from "../constants";
import { adminService } from "../modules/admin/admin.api";

@Component
export default class ConfigurationForm extends Vue {
  payload: Partial<ConfigurationModel> = {};

  mounted() {
    GlobalEvent.$on(FormEvents.SH_CONFIGURATION_FORM, (data: ConfigurationModel) => {
      this.payload = data;
      this.modal("configuration-form", true);
      this.handleModalOpen();
    });
  }

  async handleModalOpen() {
    try {
      this.isLoading = true;
      const response = await adminService.configuration();
      const { bannedEmailProviders } = response;
      this.payload = {
        ...response,
        bannedEmailProvidersInput: bannedEmailProviders
          ? bannedEmailProviders?.map(x => {
              return {
                value: x
              };
            })
          : []
      };
      this.isLoading = false;
    } catch (error) {
      this.handleError(error);
    }
  }

  async save(payload: ConfigurationModel) {
    try {
      await this.validate();
      this.isLoading = true;

      await adminService.updateConfiguration({
        ...payload,
        bannedEmailProviders: payload.bannedEmailProvidersInput.map(x => x.value)
      });

      this.toastSuccess("Saved");
      this.modal("configuration-form", false);
      this.isLoading = false;
    } catch (error) {
      this.handleError(error);
    }
  }

  addEmailProvider() {
    this.payload.bannedEmailProvidersInput?.push({
      value: null
    });
  }
}
