














































































































































import { mixins } from "vue-class-component";
import { Component, Vue } from "vue-property-decorator";
import { CampaignModel } from "../models/Campaign";
import { adminService } from "../modules/admin/admin.api";
import { DataTableMixin } from "../shared/mixins/datatable.mixin";
import StarRating from "vue-star-rating";

@Component({
  components: {
    StarRating
  }
})
export default class SingleCampaign extends mixins(DataTableMixin) {
  campaign: Partial<CampaignModel> = {
    listensCount: 0,
    budget: 0,
    allocatedListens: 0,
    song: {
      title: "",
      id: "",
      createdAt: new Date(),
      updatedAt: new Date(),
      userId: "",
      genreId: "",
      musicLinks: [],
      link: "",
      artisteName: "",
      deletedAt: new Date()
    }
  };

  isLoading = true;

  isTableFetching = false;

  reviews: any[] = [];

  headers = [
    // {
    //   name: "User",
    //   prop: "user.fullName"
    // },
    {
      name: "Rating",
      prop: "rating"
    },
    {
      name: "Feedback",
      prop: "body"
    }
  ];

  averageRating: number = 0;

  // get averageRating() {
  //   return (5 * Number(this.pagination.total)) / Number(this.Rating);
  // }

  mounted() {
    this.getCampaign();
  }

  async getCampaign() {
    try {
      this.isLoading = true;
      const { campaignId } = this.$route.params;
      const response = await adminService.fetchCampaign(campaignId);
      this.campaign = response.data;

      this.isLoading = false;
      this.fetchData();
    } catch (error) {
      this.handleError(error);
    }
  }
}
