






























































































import { Component, Vue } from "vue-property-decorator";
import { CampaignModel } from "../models/Campaign";
import { MusicPlatformModel } from "../models/MusicPlatform";
import { apiConfigService } from "../config/api.config";
import { GlobalEvent, responseHandler } from "../shared/helpers";
import { FormEvents } from "../constants";
import { adminService } from "../modules/admin/admin.api";
import { SongModel } from "../models/Song";

@Component
export default class CampaignForm extends Vue {
  songs: SongModel[] = [];
  musicPlatforms: MusicPlatformModel[] = [];

  payload: Partial<CampaignModel> = {
    budget: 0,
    songId: "",
    musicPlatformId: "",
    amountPerListen: null,
    listenBudgetPercentage: null
  };

  get songPlatforms() {
    const selectedSongLinks = this.songs.find(x => x.id === this.payload.songId)?.musicLinks;
    const selectedSongPlatforms = selectedSongLinks?.filter(x => x.link).map(x => x.musicPlatformId);
    const musicPlatforms = this.musicPlatforms.filter(x => selectedSongPlatforms?.includes(x.id));
    return musicPlatforms;
  }

  async save(payload: CampaignModel) {
    try {
      // this.showLoading();
      this.isLoading = true;
      const response = await adminService.createCampaign(payload);
      this.toastSuccess(response.message);
      this.modal("campaign-form", false);
      // this.hideLoading();
      this.isLoading = false;
      this.payload = {};
      this.$emit("refresh");
    } catch (error) {
      this.handleError(error);
    }
  }

  mounted() {
    GlobalEvent.$on(FormEvents.SH_CAMPAIGN_FORM, (data: CampaignModel) => {
      this.payload = data;
      this.modal("campaign-form", true);
      this.handleModalOpen();
    });
  }

  async handleModalOpen() {
    await this.handleSongs();
    this.handleMusicPlatforms();
  }

  async handleSongs() {
    try {
      this.isLoading = true;
      const response = await adminService.fetchSongs({
        page: 1,
        limit: 1000
      });

      const songs = response.data as SongModel[];

      this.songs = songs;

      this.isLoading = false;
    } catch (error) {
      this.handleError(error);
    }
  }

  async handleMusicPlatforms() {
    try {
      const response = await apiConfigService.request().get("music-platforms");
      const output = responseHandler(response);

      const musicPlatforms = output.data as MusicPlatformModel[];
      this.musicPlatforms = musicPlatforms;
    } catch (error) {
      this.handleError(error);
    }
  }
}
