














































































































































































































import { Component, Vue, Watch } from "vue-property-decorator";
import { formatDate, GlobalEvent } from "../shared/helpers";
import { adminService } from "../modules/admin/admin.api";
import { UserModel } from "../models/User";
import { mixins } from "vue-class-component";
import { DataTableMixin } from "../shared/mixins/datatable.mixin";
import { FormEvents } from "../constants";

@Component
export default class Users extends mixins(DataTableMixin) {
  users: UserModel[] = [];

  pagination = {
    page: 1,
    pageCount: 0,
    perPage: 10,
    total: 0,
    skipped: 0,
    q: null,
    status: true,
    canWithdraw: true,
    userType: "",
    hasRunCampaigns: undefined,
    timeframe: undefined
  };

  headers = [
    {
      name: "Full Name",
      prop: "fullName"
    },
    {
      name: "Type",
      prop: "role"
    },
    {
      name: "Email",
      prop: "email"
    },
    {
      name: "Telephone",
      prop: "telephone"
    },
    {
      name: "Country",
      prop: "country"
    },
    {
      name: "Registeration Date",
      prop: "createdAt",
      formatter: (data: string) => formatDate(data, "DD MMMM YYYY, hh:mm a")
    }
  ];

  mounted() {
    this.pagination = { ...this.pagination, ...this.$route.query };
    this.handleFetchUsers();
  }

  fetchData() {
    this.handleFetchUsers();
  }

  async handleFetchUsers() {
    try {
      this.showLoading();
      const response = await adminService.fetchUsers({ ...this.pagination, limit: this.pagination.perPage });

      this.users = response.data;

      this.pagination = { ...this.pagination, ...response.pagination };
      this.hideLoading();
    } catch (error) {
      this.handleError(error);
    }
  }

  // async myCallback(page: number) {
  //   this.pagination.page = page;
  //   this.handleFetchUsers();
  // }

  // async handleLimitChange(limit: number) {
  //   this.pagination.perPage = limit;
  //   this.handleFetchUsers();
  // }

  clearFilters() {
    this.pagination.q = null;
    this.pagination.status = true;
    this.pagination.canWithdraw = true;
    this.pagination.userType = "";
    this.pagination.hasRunCampaigns = undefined;
    this.handleFetchUsers();
  }

  showDisableUserForm(userId: string) {
    GlobalEvent.$emit(FormEvents.SH_DISABLE_USER, {
      userId
    });
  }

  async enableUser(userId: string) {
    await this.confirm(() => adminService.enableUser(userId), "User is enabled");
  }
}
