
















































import { Component, Vue } from "vue-property-decorator";
import { FormEvents } from "../constants";
import { AdModel } from "../models/Ads";
import { adminService } from "../modules/admin/admin.api";
import { GlobalEvent } from "../shared/helpers";

@Component
export default class AdForm extends Vue {
  payload = {
    id: null,
    description: null,
    name: null,
    link: null,
    coverImage: null
  };

  coverImage!: File;

  get isUpdate() {
    return this.payload.id ? true : false;
  }

  mounted() {
    GlobalEvent.$on(FormEvents.SH_AD_FORM, (data: any) => {
      this.payload = data;
      this.modal("ad-form", true);
    });
  }

  async save(payload: AdModel) {
    try {
      // this.showLoading();
      this.isLoading = true;
      const formData = new FormData();
      formData.append("description", payload.description);
      formData.append("name", payload.name);
      formData.append("link", payload.link);
      formData.append("coverImage", this.coverImage);

      const response = !this.isUpdate
        ? await adminService.createAd(formData)
        : await adminService.updateAd(payload.id, formData);

      this.toastSuccess(response.message);
      this.modal("ad-form", false);
      // this.hideLoading();
      this.isLoading = false;

      this.payload = {
        id: null,
        description: null,
        name: null,
        link: null,
        coverImage: null
      };

      const coverImageRef: any = this.$refs.coverImage;
      coverImageRef.value = null;
      this.$emit("refresh");
    } catch (error) {
      this.handleError(error);
    }
  }

  uploadFile(file: File) {
    this.coverImage = file;
  }
}
