


























































import { mixins } from "vue-class-component";
import { Component } from "vue-property-decorator";
import { AdModel } from "../models/Ads";
import { adminService } from "../modules/admin/admin.api";
import { DataTableMixin } from "../shared/mixins/datatable.mixin";
import AdForm from "../modals/AdForm.vue";
import { FormEvents } from "../constants";
import { GlobalEvent } from "../shared/helpers";

@Component({
  components: {
    AdForm
  }
})
export default class Ads extends mixins(DataTableMixin) {
  ads: AdModel[] = [];

  headers = [
    {
      name: "Title",
      prop: "name"
    },
    {
      name: "Link",
      prop: "link"
    },
    {
      name: "Status",
      prop: "enabled"
    },
    {
      name: "Date",
      prop: "createdAt",
      formatter: (data: string) => this.formatDate(data, "DD MMMM YYYY, hh:mm a")
    }
  ];

  fetchData() {
    this.handleAds();
  }

  mounted() {
    this.handleAds();
  }

  async handleAds() {
    try {
      this.showLoading();
      const response = await adminService.fetchAds({
        page: this.pagination.page,
        limit: this.pagination.perPage
      });

      this.ads = response.data;

      this.pagination = response.pagination;
      this.hideLoading();
    } catch (error) {
      this.handleError(error);
    }
  }

  async showModal(item: AdModel) {
    GlobalEvent.$emit(FormEvents.SH_AD_FORM, item);
  }
}
