


























































import { Component, Vue } from "vue-property-decorator";
import { FormEvents } from "../constants";
import { CampaignModel, CampaignStatus } from "../models/Campaign";
import { adminService } from "../modules/admin/admin.api";
import { GlobalEvent } from "../shared/helpers";

@Component
export default class SingleCampaign extends Vue {
  campaign: Partial<CampaignModel> = {
    song: {
      title: "",
      id: "",
      createdAt: new Date(),
      updatedAt: new Date(),
      userId: "",
      genreId: "",
      link: "",
      musicLinks: [],
      artisteName: ""
    },
    listensCount: 0,
    allocatedListens: 0,
    amountPerListen: 0,
    budget: 0
  };

  mounted() {
    GlobalEvent.$on(FormEvents.SH_CAMPAIGN, (id: string) => {
      this.modal("single-campaign", true);
      this.handleModalOpen(id);
    });
  }

  async handleModalOpen(id: string) {
    try {
      this.isLoading = true;
      const response = await adminService.fetchCampaign(id);
      this.campaign = response.data;
      this.isLoading = false;
    } catch (error) {
      this.handleError(error);
    }
  }

  getStatusColor(status: CampaignStatus) {
    let color: string = "";
    switch (status) {
      case CampaignStatus.PENDING:
        color = "alert-warning";
        break;
      case CampaignStatus.ACTIVE:
        color = "alert-success";
        break;
      case CampaignStatus.CANCELLED:
        color = "alert-danger";
        break;
      case CampaignStatus.COMPLETED:
        color = "alert-info";
        break;

      case CampaignStatus.AWAITING_PAYMENT:
        color = "alert-secondary";
        break;
      default:
        break;
    }

    return color;
  }
}
